<template>
	<n-space vertical size="large">
		<n-layout has-sider style="height: 88vh;">
			<n-layout-sider
				collapse-mode="width"
				:collapsed-width="0"
				:native-scrollbar="false"
				:width="240"
				:show-collapsed-content="false"
				show-trigger="arrow-circle"
				content-style="padding-left: 30px;padding-right: 24px;"
				bordered
			>
				<h3>文档列表: </h3>
				<br>
				<div style="width: 175px;">
					<span v-html="MarkdownsInside"></span>
				</div>
			</n-layout-sider>
			<n-layout-content content-style="padding-left: 24px; padding-right: 24px;" :native-scrollbar="false">
				<div class="markdown-body">
					<markdown />
				</div>
			</n-layout-content>
			<div class="SideTool" style="width: 200px;padding-left: 6px;padding-right: 24px;">
				<p style="color: #6E6E6E;">这里有点东西会好看一点</p>
			</div>
		</n-layout>
	</n-space>
	
</template>

<script>
import markdown from '@/components/WebInfo.md'
import 'highlight.js/styles/github.css'
import 'github-markdown-css'

const files = require.context('@/components/Markdowns', false, /.md$/) //寻找 md 文件
files.keys().forEach(path=> {console.log(path);});//告诉控制台已经有md文件
var MarkFiles = ""
for (var i=0;i<files.keys().length;i++){
	MarkFiles = MarkFiles + 
	"<a class=\"Marklink\" herf=\"" + 
	files.keys()[i].substring(5,files.keys()[i].length) + 
	"\">" + 
	files.keys()[i].substring(2,files.keys()[i].length) + 
	"<br>"
}//输出列表

export default {
	components: {
		markdown
	},
	data(){
		return{
			MarkdownsInside: MarkFiles
		}
	},
}
</script>

<style>
	.markdown-body{
		margin-top:-24px;
		padding-left: 24px;
	}
	.SideTool{
		z-index: 1;
		font-size: 15px;
        padding-right: 50px;
        color: #000000;
        position: -webkit-sticky;
        position: sticky;
	}
    .Marklink{
        font-size: 15px;
        padding-right: 50px;
        color: #6E6E6E;
    }
    .Marklink:hover{
        /*color: #7B00E6;*/
        color: #8491A3;
    }
</style>